.App {
  text-align: center;
}

.App-logo {
  position: absolute;
  height: 96vmin;
  top: 0;
  left: 0px;
  pointer-events: none;
  align-self: flex-start;
  margin-top: -11.5vmin;
}

.App-title {
  font-size: 28vmin;
  padding-right: 2vw;
  font-weight: bolder;
  font-family: 'Lato', serif;
  letter-spacing: -0.04em;
  align-self: flex-end;
  color: #fff;
}

.App-subtitle {
  font-size: 11vmin;
  padding-right: 2vw;
  padding-bottom: 4vmin;
  font-weight: bolder;
  font-family: 'Lato', serif;
  letter-spacing: -0.04em;
  align-self: flex-end;
  color: #fff;
  display: flex;
}

.App-project {
  font-size: 7vmin;
  padding-right: 2vw;
  padding-bottom: 4vmin;
  font-weight: bolder;
  font-family: 'Lato', serif;
  letter-spacing: -0.04em;
  align-self: flex-end;
  color: #fff;
  display: flex;
}

.App-link {
  font-size: 5vmin;
  padding-left: 2vmin;
  font-weight: bolder;
  font-family: 'Lato', serif;
  letter-spacing: -0.04em;
  align-self: flex-end;
  color: #fff;
}

.App-links {
  font-size: 2vmin;
  padding-right: 2vw;
  padding-bottom: 20vmin;
  font-weight: bolder;
  font-family: 'Lato', serif;
  letter-spacing: -0.04em;
  align-self: flex-end;
  color: #fff;
  display: flex;
}

.App-header {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}